import { Core, Fetch } from "@true/core";
import { batch } from "react-redux";
import { IOffice } from "@policy/store/offices/types";
import { setOfficesQuery, syncOffices } from "@policy/store/offices/index";

interface IParams {}

export const requestOffices = async (params?: IParams) => {
  const {data} = await Fetch.postWithToken<{
    offices: IOffice[];
  }>(`@${Core.systemPath}/policy/ajax/api/mobile/offices/list`, params);

  batch(() => {
    if (data.offices) {
      syncOffices(data.offices);
      setOfficesQuery({
        all: data.offices.map(item => item.id),
      });
    }
  });
};
