import {policiesReducer, setPoliciesStore} from '@policy/store/policies';
import {doctypesReducer, setDoctypesStore} from '@policy/store/doctypes';
import {setStoragesStore, storagesReducer} from '@policy/store/storages';
import {postsReducer, setPostsStore} from '@policy/store/posts';
import {
  detailPolicyReducer,
  setDetailPolicyStore,
} from '@policy/store/detailPolicy';
import {setOfficesStore, officesReducer} from '@policy/store/offices';
import {collectionsReducer, setCollectionsStore} from './collections';
import {
  masterVersionDoctypesReducer,
  setMasterVersionDoctypesStore,
} from '@policy/store/masterVersionDoctypes';
import {favDocsReducer, setFavDocsStore} from '@policy/store/favDocs';
import {setupStore} from '@true/core';
import {aclReducer, setAclStore} from '@policy/store/acl';

export const policyReducers = {
  acl: aclReducer,
  policies: policiesReducer,
  doctypes: doctypesReducer,
  storages: storagesReducer,
  posts: postsReducer,
  detailPolicy: detailPolicyReducer,
  offices: officesReducer,
  collections: collectionsReducer,
  masterVersionDoctypes: masterVersionDoctypesReducer,
  favDocs: favDocsReducer,
};

export const setPolicyStore = (store: any) => {
  setAclStore(store);
  setPoliciesStore(store);
  setDoctypesStore(store);
  setStoragesStore(store);
  setPostsStore(store);
  setDetailPolicyStore(store);
  setOfficesStore(store);
  setCollectionsStore(store);
  setMasterVersionDoctypesStore(store);
  setFavDocsStore(store);
};

/* -- setup store -- */
export const {persistor} = setupStore(policyReducers, setPolicyStore);
