import React, {memo, useMemo} from 'react';
import {Colors, Fonts, getWidth, styled} from '@true/core';
import {IPolicy} from '@policy/store/policies/types';
import {IDetailPolicy} from '@policy/store/detailPolicy/types';
import {
  IMG_PENDING_BADGE,
  IMG_PUBLISHED_BADGE,
  IMG_REJECTED_BADGE,
} from '@true/asset';

export const getStatus = (policy?: IDetailPolicy | IPolicy) => {
  if (policy?.status === 1)
    return {
      text: 'Published',
      color: Colors.green1,
      bgColor: Colors.green3,
      icon: IMG_PUBLISHED_BADGE,
    };
  if (policy?.status === 0)
    return {
      text: 'Pending',
      color: Colors.orange1,
      bgColor: Colors.orange3,
      icon: IMG_PENDING_BADGE,
    };
  if (policy?.status === -1)
    return {
      text: 'Rejected',
      color: Colors.red1,
      bgColor: Colors.red3,
      icon: IMG_REJECTED_BADGE,
    };
  return {text: 'Neh', color: Colors.neutral1, bgColor: Colors.neutral5};
};

export const PolicyStatuses = memo(
  (props: {policy?: IDetailPolicy | IPolicy}) => {
    const {policy} = props;

    const status = useMemo(() => {
      return getStatus(policy);
    }, [policy]);

    return (
      <SRow>
        <SStatusTag style={{backgroundColor: status.bgColor}}>
          <SStatusText style={{color: status.color}}>{status.text}</SStatusText>
        </SStatusTag>
        {policy?.scode ? (
          <SScodeTag>
            <SScodeText>{policy?.scode}</SScodeText>
          </SScodeTag>
        ) : null}
      </SRow>
    );
  },
);

const SRow = styled.View`
  flex-direction: row;
  align-items: flex-start;
  flex-wrap: wrap;
  max-width: ${getWidth(300)}px;
`;

const SStatusTag = styled.View`
  padding: 4px 8px;
  border-radius: 4px;
  margin-right: 8px;
  margin-bottom: 8px;
`;
const SStatusText = styled.Text`
  font-size: 12px;
  line-height: 16px;
  font-family: ${Fonts.Medium};
`;

const SScodeTag = styled.View`
  padding: 4px 8px;
  border-radius: 4px;
  background-color: ${p => p.theme.neutral5};
  margin-bottom: 8px;
  //flex-shrink: 1;
`;
const SScodeText = styled.Text`
  color: ${p => p.theme.neutral1};
  font-size: 12px;
  line-height: 16px;
`;
