import {
  createContext,
  Dispatch,
  PropsWithChildren,
  SetStateAction,
  useContext,
  useMemo,
  useState,
} from 'react';
import {SharedValue, useSharedValue} from 'react-native-reanimated';

interface IReturnValue {
  selectedIds: SharedValue<number[]>;
  selectedState: boolean;
  setSelectedState: Dispatch<SetStateAction<boolean>>;
}

const SelectedItemContext = createContext<IReturnValue | null>(null);

export const SelectedItemProvider = (props: PropsWithChildren) => {
  const selectedIds = useSharedValue<number[]>([]);
  const [selectedState, setSelectedState] = useState(false);

  const returnValue = useMemo(
    () => ({
      selectedIds,
      selectedState,
      setSelectedState,
    }),
    [selectedIds, selectedState, setSelectedState],
  );

  // const {setBottomTabVisible} = useBottomTabBar();
  //
  // useAnimatedReaction(
  //   () => selectedIds.value.length,
  //   _length => {
  //     if (_length) {
  //       setBottomTabVisible(2);
  //     } else {
  //       setBottomTabVisible(1);
  //     }
  //   },
  //   [],
  // );

  return (
    <SelectedItemContext.Provider value={returnValue}>
      {props.children}
    </SelectedItemContext.Provider>
  );
};

export const useSelectedItem = () => {
  const value = useContext(SelectedItemContext);
  if (value == null)
    return {
      selectedIds: useSharedValue<number[]>([]),
      selectedState: false,
      setSelectedState: (value: any) => {},
    };
  // throw new Error('SelectedItemProvider value is null');
  return useContext(SelectedItemContext) as IReturnValue;
};
