import {IC_X, styled, translate, useBoolean, useTheme} from '@true/core';
import React, {memo, useCallback, useState} from 'react';
import Animated, {
  SharedValue,
  useAnimatedProps,
  useAnimatedStyle,
  useDerivedValue,
} from 'react-native-reanimated';
import {StyleSheet, View, ViewProps} from 'react-native';
import {useSelectedItem} from '@policy/context/SelectedItemContext';
import {ReText} from 'react-native-redash';
import {Button} from '@policy/components/Button';
import {
  requestBookmark,
  requestMarkRead,
} from '@policy/store/policies/functions';
import {useAsyncFn} from 'react-use';
import {
  IC_OUTLINED_BOOKMARK,
  IC_OUTLINED_DOC_MARK_READ,
  IC_OUTLINED_REMOVE_BOOKMARK,
} from '@true/asset';
import {MyConfirmationDialog} from '@policy/components/Dialog/MyConfirmationDialog';
import {useAcl} from '@policy/store/acl';
import {toBoolean} from '@true/ui';

export enum SELECTED_TYPE {
  mark_read = 'MARK_READ',
  bookmark = 'BOOKMARK',
  remove_bookmark = 'REMOVE_BOOKMARK',
}

export const SelectedHeader = memo(
  ({
    animatedTop,
    animatedHeight,
  }: {
    title: string;
    animatedTop?: SharedValue<number>;
    animatedHeight?: SharedValue<number>;
  } & ViewProps) => {
    const [visible, show, hide] = useBoolean();
    const theme = useTheme();
    const {selectedIds, setSelectedState, selectedState} = useSelectedItem();
    const [type, setType] = useState<SELECTED_TYPE>();
    const isManager = useAcl()?.managed;

    const onPress = useCallback(
      (typeSet?: SELECTED_TYPE) => {
        setType(typeSet);
        show();
      },
      [show],
    );

    const animatedProps = useAnimatedProps(
      () =>
        ({
          pointerEvents: selectedState ? 'auto' : 'none',
        } as any),
    );

    const animatedText = useDerivedValue(() =>
      selectedIds.value.length ? ` (${selectedIds.value.length})` : '',
    );

    const animatedHeaderStyle = useAnimatedStyle(() => {
      return {
        height: animatedHeight?.value || 40,
        top: animatedTop?.value || 0,
      };
    });

    const onCloseHeader = useCallback(() => {
      selectedIds.value = [];
      setSelectedState(false);
      hide();
    }, [hide, selectedIds, setSelectedState]);

    const [{loading: loadingMarkRead}, onReadMark] = useAsyncFn(async () => {
      await requestMarkRead({
        ids: selectedIds.value,
      });
      onCloseHeader();
    }, [selectedIds]);

    const [{loading:loadingBookmark}, onBookmark] = useAsyncFn(async () => {
      await requestBookmark({
        ids: selectedIds.value,
        action: 'bookmark',
      });
      onCloseHeader();
    }, [selectedIds]);

    const [{loading}, onRemoveBookmark] = useAsyncFn(async () => {
      await requestBookmark({
        ids: selectedIds.value,
        action: 'remove.bookmark',
      });
      onCloseHeader();
    }, [selectedIds]);

    const getType = (type?: SELECTED_TYPE) => {
      if (type === SELECTED_TYPE.mark_read)
        return {
          title: translate('policy.confirm_mark_read'),
          content: translate('policy.mark_read_content'),
          onPress: onReadMark,
        };
      if (type === SELECTED_TYPE.bookmark)
        return {
          title: translate('policy.confirm_bookmark'),
          content: translate('policy.bookmark_content'),
          onPress: onBookmark,
        };
      if (type === SELECTED_TYPE.remove_bookmark)
        return {
          title: translate('policy.confirm_remove_bookmark'),
          content: translate('policy.remove_bookmark_content'),
          onPress: onRemoveBookmark,
        };
      return {title: 'Neh', content: 'NAh', onPress: () => {}};
    };

    return (
      <SContainer
        animatedProps={animatedProps}
        style={[{opacity: selectedState ? 1 : 0}, animatedHeaderStyle]}>
        <SActionWrapper>
          <SButton source={IC_X} onPress={onCloseHeader} />
          <STextWrapper>
            <SText>{translate('policy.selected')}</SText>
            <SAnimatedText text={animatedText} />
          </STextWrapper>
          <View style={{flex: 1}} />
          {toBoolean(isManager) ? (
            <SButton
              source={IC_OUTLINED_REMOVE_BOOKMARK}
              onPress={() => onPress(SELECTED_TYPE.remove_bookmark)}
              hideContentWhileLoading={true}
            />
          ) : null}
          {toBoolean(isManager) ? (
            <SButton
              style={styles.leftButton}
              source={IC_OUTLINED_BOOKMARK}
              onPress={() => onPress(SELECTED_TYPE.bookmark)}
              hideContentWhileLoading={true}
            />
          ) : null}
          <SButton
            style={styles.leftButton}
            source={IC_OUTLINED_DOC_MARK_READ}
            onPress={() => onPress(SELECTED_TYPE.mark_read)}
            hideContentWhileLoading={true}
          />
        </SActionWrapper>

        <MyConfirmationDialog
          isVisible={visible}
          onCloseRequest={hide}
          title={getType(type).title}
          content={getType(type).content}
          onPressRightButton={getType(type).onPress}
          colorLeftButton={theme.neutral2}
          bgColorLeftButton={theme.white}
          titleRightButton={translate('policy.confirm')}
        />
      </SContainer>
    );
  },
);

const SContainer = styled(Animated.View)`
  position: absolute;
  left: 0;
  right: 0;
  background-color: ${p => p.theme.backgroundColor};
  z-index: 99;
  border-bottom-width: 1px;
  border-color: ${p => p.theme.neutral5};
`;

const STextWrapper = styled.View`
  align-items: center;
  flex-direction: row;
  margin-right: 16px;
  margin-left: 6px;
`;

const SAnimatedText = styled(ReText)`
  font-size: 14px;
  line-height: 16px;
  color: ${p => p.theme.neutral2};
`;

const SText = styled.Text`
  font-size: 14px;
  line-height: 20px;
  color: ${p => p.theme.neutral2};
`;

const SActionWrapper = styled.View`
  flex-direction: row;
  padding: 12px 16px;
  background-color: ${p => p.theme.backgroundColor};
`;

const SButton = styled(Button).attrs(p => ({
  imageStyle: {
    tintColor: p.theme.neutral1,
  },
}))``;

const styles = StyleSheet.create({
  leftButton: {
    marginLeft: 24,
  },
});
