import {Core, Fetch} from '@true/core';
import {setPoliciesQuery, syncPolicies} from '@policy/store/policies/index';
import {batch} from 'react-redux';
import {IDetailPolicy} from '@policy/store/detailPolicy/types';
import {requestLoadFavDocs} from '@policy/store/favDocs/functions';

interface IRequestPolicyParams {
  tab?: 'pending' | 'attesting' | 'following';
  page?: number;
}

export const requestPolicy = async (params: IRequestPolicyParams) => {
  await requestLoadFavDocs();

  const {data} = await Fetch.postWithToken<{
    docs: IDetailPolicy[];
  }>(`@${Core.systemPath}/policy/ajax/api/mobile/doc/list`, params);

  batch(() => {
    if (data.docs) {
      syncPolicies(data.docs);

      const newItems = data.docs.map(item => item.id.toString());

      setPoliciesQuery(prev => ({
        ...prev,
        [params.tab || 'all']:
          params.page && params.page > 1
            ? [...prev[params.tab || 'all'], ...newItems]
            : newItems,
      }));
    }
  });

  return data.docs;
};

export interface IRequestSearchPolicyParams {
  storage: string;
  receiver: string;
  collection: string;
  folder: string;
  doctype: string;
  created_date_from: null | number;
  created_date_to: null | number;
  effective_date_from: null | number;
  effective_date_to: null | number;
  expired_date_from: null | number;
  expired_date_to: null | number;
  q: string;
  created_by: null | number;
  tab?: string;
  page?: number;
}

export const requestSearchPolicy = async (
  params: IRequestSearchPolicyParams,
) => {
  await requestLoadFavDocs();

  const {data} = await Fetch.postWithToken<{
    docs: IDetailPolicy[];
  }>(`@${Core.systemPath}/policy/ajax/api/mobile/doc/search`, params);

  batch(() => {
    if (data.docs) {
      syncPolicies(data.docs);
      const newDocs = data.docs.map(item => item.id.toString());
      const tabStr = params.tab ? 'search/' + params.tab : 'all';

      setPoliciesQuery(prev => ({
        ...prev,
        [tabStr]:
          params.page !== undefined && params.page > 1
            ? [...(prev?.[tabStr] || {}), ...newDocs]
            : newDocs,
      }));
    }
  });

  return data.docs;
};

