interface IRequestMarkRead {
  ids: number[];
}

export const requestMarkRead = async (params: IRequestMarkRead) => {
  // console.log('params', params);
  const {data} = await Fetch.postWithToken<{
    message: 'Success' | '';
  }>(`@${Core.systemPath}/policy/ajax/api/docs/mark.read`, params);

  batch(() => {
    if (data.message === 'Success') {
      syncPolicies(prev => {
        return params['ids'].map(_id => ({...prev[_id], seen: 1}));
      });
    }
  });
};

export const requestBookmark = async (params: {
  ids: number[];
  action: string;
}) => {
  const {data} = await Fetch.postWithToken<{
    message: 'Success' | '';
  }>(`@${Core.systemPath}/policy/ajax/api/docs/bookmark`, params);

  batch(() => {
    if (data.message === 'Success' && params['action'] === 'bookmark') {
      syncPolicies(prev => {
        return params['ids'].map(_id => ({...prev[_id], bookmarked: 1}));
      });
    }
    if (data.message === 'Success' && params['action'] === 'remove.bookmark') {
      syncPolicies(prev => {
        return params['ids'].map(_id => ({...prev[_id], bookmarked: 0}));
      });
    }
  });
};

export const requestApprove = async (params: {
  id: number | string;
  status: -1 | 1;
  note?: string;
}) => {
  const {data} = await Fetch.postWithToken<{
    doc: IDetailPolicy;
  }>(`@${Core.systemPath}/policy/ajax/api/doc/approve`, params);

  batch(() => {
    if (data.doc) {
      syncPolicies([data.doc]);
      setPoliciesQuery(prev => ({
        ...prev,
        pending: [...prev.pending].filter(
          _id => _id.toString() != params.id.toString(),
        ),
        all: data.doc.status == 1 ? [params.id, ...prev.all] : prev.all,
      }));
    }
  });
};

export const requestPermantRemove = async (params: {
  ids: (number | string)[];
}) => {
  const data = await Promise.all(
    params.ids.map(item => {
      return Fetch.postWithToken<{
        doc: IDetailPolicy;
      }>(`@${Core.systemPath}/policy/ajax/api/doc/permanently.delete`, {
        id: item,
      });
    }),
  );

  return data;
};

