export const requestPermanentlyDeletePolicy = async (params: {id: string}) => {
  const {data} = await Fetch.postWithToken<{
    doc: IDetailPolicy;
  }>(`@${Core.systemPath}/policy/ajax/api/doc/permanently.delete`, params);

  batch(() => {
    if (data.doc) {
      setPoliciesQuery(prev => {
        let newQueries: {[id: string]: (string | number)[]} = {};

        for (let query of Object.keys(prev)) {
          newQueries[query] = [
            ...prev[query].filter(_id => _id !== data.doc.id),
          ];
        }

        return {
          ...prev,
          ...newQueries,
        };
      });
    }
  });

  return data.doc;
};
