import {Core, Fetch} from '@true/core';
import {syncFavDocs} from './index';

export const requestLoadFavDocs = async () => {
  const {data} = await Fetch.postWithToken<{
    fav_docs: number[];
  }>(`@${Core.systemPath}/policy/ajax/api/mobile/me/init`, {});

  syncFavDocs(data?.fav_docs || []);
  return data?.fav_docs || [];
};

export const requestFavDoc = async (params: {id: number|string}) => {
  const {data} = await Fetch.postWithToken<{
    added: '0' | '1';
  }>(`@${Core.systemPath}/policy/ajax/api/doc/fav`, params);

  syncFavDocs(prev => {
    if (data?.added == '0') return prev.filter(item => item != params.id);
    if (data?.added == '1') return [...prev, parseInt(params.id as string)];
    return prev;
  });
};
