import {Core, Fetch} from '@true/core';
import { batch } from "react-redux";
import { IDocTypes } from "@policy/store/doctypes/types";
import { setDoctypesQuery, syncDoctypes } from "@policy/store/doctypes/index";

interface IParams {}

export const requestDoctypes = async (params?: IParams) => {
  const {data} = await Fetch.postWithToken<{
    doctypes: IDocTypes[]
  }>(`@${Core.systemPath}/policy/ajax/api/mobile/doctype/list`, params);

  batch(() => {
    if (data.doctypes) {
      syncDoctypes(data.doctypes);
      setDoctypesQuery({
        all: data.doctypes.map(item => item.id),
      });
    }
  })
};
