import {Core, Fetch} from '@true/core';
import {batch} from 'react-redux';
import {IStorages} from '@policy/store/storages/types';
import {setStoragesQuery, syncStorages} from '@policy/store/storages/index';

interface IParams {}

export const requestStorages = async (params?: IParams) => {
  const {data} = await Fetch.postWithToken<{
    storages: IStorages[];
  }>(`@${Core.systemPath}/policy/ajax/api/mobile/storage/list`, params);

  batch(() => {
    if (data.storages) {
      syncStorages(data.storages);
      setStoragesQuery({
        all: data.storages.map(item => item.id),
      });
    }
  });
};
