import {memoForwardRef, flatStyles, interactManager} from '@true/core';
import {
  ActivityIndicator,
  Image,
  ImageProps,
  ImageStyle,
  Text,
  TextProps,
  TextStyle,
  TouchableOpacity,
  TouchableOpacityProps,
  View,
  ViewStyle,
} from 'react-native';
import React, {RefObject, useCallback} from 'react';
import {useButton} from '@policy/components/Button/context';
import Animated, {
  useAnimatedProps,
  useSharedValue,
} from 'react-native-reanimated';

interface Props extends TouchableOpacityProps {
  title?: string;
  loading?: boolean;
  style?: ViewStyle;
  textProps?: TextProps;
  textStyle?: TextStyle;
  hideContentWhileLoading?: boolean;
  source?: any;
  imageStyle?: ImageStyle;
  imageProps?: ImageProps;
  imageSize?: number;
  wait?: number;
  timeout?: RefObject<any>
}

const AnimatedTouchOpacity = Animated.createAnimatedComponent(TouchableOpacity);

const hitSlopVal = (imageSize: number) =>
  imageSize < 40 ? (40 - imageSize) / 2 : imageSize;

export const Button = memoForwardRef(
  ({
    title,
    loading,
    textProps,
    textStyle,
    hideContentWhileLoading,
    source,
    imageStyle,
    imageProps,
    imageSize = 24,
    onPress,
    wait = 600,
    timeout,
    ...rest
  }: Props) => {
    const {disabled} = useButton();

    const _disabled = useSharedValue(false);

    const _onPress = useCallback(
      (e: any) => {
        if (onPress) {
          if (
            !_disabled.value &&
            !disabled?.value &&
            !loading &&
            !rest.disabled
          ) {
            onPress(e);
          }

          _disabled.value = true;
          if (disabled) disabled.value = true;

          interactManager(
            () => {
              _disabled.value = false;
              if (disabled) disabled.value = false;
            },
            wait,
            true,
            timeout
          );
        }
      },
      [wait, _disabled, disabled, loading, rest.disabled, onPress],
    );

    return (
      <AnimatedTouchOpacity
        hitSlop={{
          top: hitSlopVal(imageSize),
          left: hitSlopVal(imageSize),
          right: hitSlopVal(imageSize),
          bottom: hitSlopVal(imageSize),
        }}
        {...rest}
        style={rest.style}
        onPress={_onPress}>
        <View
          style={{
            flexDirection: 'row',
            opacity: hideContentWhileLoading && loading ? 0 : 1,
          }}>
          {title ? (
            <Text style={textStyle} {...textProps}>
              {title}
            </Text>
          ) : null}
          {source ? (
            <Image
              source={source}
              style={flatStyles(imageStyle, {
                width: imageSize,
                height: imageSize,
              })}
              {...imageProps}
            />
          ) : null}
          {rest.children}
        </View>
        <View
          style={{
            position: 'absolute',
            top: 0,
            bottom: 0,
            left: 0,
            right: 0,
            alignItems: 'center',
            justifyContent: 'center',
            opacity: loading ? 1 : 0,
          }}>
          <ActivityIndicator />
        </View>
      </AnimatedTouchOpacity>
    );
  },
);
