import React, {memo} from 'react';
import FastImage from 'react-native-fast-image';
import Animated, {useAnimatedStyle} from 'react-native-reanimated';
import {useAnimatedSafeAreaInsets} from 'react-native-safe-area-context';
import {StyleSheet} from 'react-native';
import {FilterIcon} from '@policy/components/FilterIcon';
import {navigateToHomeFilter, navigateToSearchScreen} from '@policy/utils';
import {Fonts, IC_SEARCH, styled, goBack, translate, useMe} from '@true/core';
import {Avatar, UIButton} from '@true/ui';
import {useSetStatusBar} from '@policy/hooks/useSetStatusBar';
import {IC_OUTLINED_LEFT} from "@true/asset";

interface HomeHeaderProps {
  params: any;
  setParams: any;
}

export const HomeHeader = memo(({params, setParams}: HomeHeaderProps) => {
  const {aTop} = useAnimatedSafeAreaInsets();
  const me = useMe();

  const animatedStyle = useAnimatedStyle(() => {
    return {
      marginTop: aTop.value,
    };
  });

  useSetStatusBar();

  return (
    <SWrapper>
      <SContainer
        style={{
          ...StyleSheet.absoluteFillObject,
          zIndex: 10,
        }}>
        <SHeaderContainer style={animatedStyle}>
          <SButtonAvatar onPress={goBack}>
            {/*<Avatar userKey={me?.id || ''} size={32} disableClick />*/}
            <SIcon source={IC_OUTLINED_LEFT} />
          </SButtonAvatar>
          <SAppName>{translate('policy.policy')}</SAppName>
          <FilterIcon
            excepts={['advanced_export']}
            params={params}
            onPress={() => navigateToHomeFilter({params, setParams})}
            colorFilterIcon={'#fff'}
          />
        </SHeaderContainer>
        <SSearchButton onPress={navigateToSearchScreen}>
          <SSearchTitle>{translate('policy.search_policy')}</SSearchTitle>
          <SSearchIcon source={IC_SEARCH} />
        </SSearchButton>
      </SContainer>
      <SBgHeader />
    </SWrapper>
  );
});

const SWrapper = styled.View`
  background-color: #1c8594;
`;

const SBgHeader = styled(FastImage)`
  height: 147px;
  width: 100%;
`;

const SContainer = styled.View`
  z-index: 10;
  border-radius: 12px;
  padding: 0 16px;
  margin-bottom: 12px;
`;

const SAppName = styled.Text`
  font-family: ${Fonts.Medium};
  font-size: 18px;
  line-height: 24px;
  color: #fff;
`;

const SHeaderContainer = styled(Animated.View)`
  flex: 1;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
`;

const SButtonAvatar = styled.TouchableOpacity``;

const SSearchButton = styled(UIButton)`
  height: 36px;
  flex-direction: row;
  background-color: #fff;
  border-radius: 8px;
  padding: 8px 12px;
  justify-content: space-between;
  align-items: center;
`;

const SSearchTitle = styled.Text`
  font-size: 14px;
  line-height: 20px;
  font-family: ${Fonts.Regular};
  color: #9f9fa3;
  flex: 1;
`;

const SSearchIcon = styled(UIButton).attrs(p => ({
  imageStyle: {
    tintColor: p.theme.neutral2,
  },
}))`
  width: 20px;
  height: 20px;
`;

const SIcon = styled(FastImage).attrs(p => ({
  tintColor: p.theme.white,
}))`
  width: 24px;
  height: 24px;
`;
