import { Core, Fetch } from "@true/core";
import { batch } from "react-redux";
import { ICollection } from "@policy/store/collections/types";
import { setCollectionsQuery, syncCollections } from "@policy/store/collections/index";

interface IParams {}

export const requestCollections = async (params?: IParams) => {
  const {data} = await Fetch.postWithToken<{
    collections: ICollection[];
  }>(`@${Core.systemPath}/policy/ajax/api/mobile/doctree/list`, params);
  batch(() => {
    if (data.collections) {
      syncCollections(data.collections);
      setCollectionsQuery({
        all: data.collections.map(item => item.id),
      });
    }
  });

  return data.collections;
};
