import React, {memo, PropsWithChildren} from 'react';
import Modal from 'react-native-modal';
import styled from 'styled-components/native';
import {StyleSheet} from 'react-native';
import {Fonts, translate, useTheme} from '@true/core';
import {IC_OUTLINED_CLOSE} from '@true/asset';
import {UIBottomButton, UIButton} from '@true/ui';

interface Props {
  isVisible: boolean;
  onCloseRequest: () => any;
  title?: string;
  content?: string;
  subContent?: string;
  disableSwipe?: boolean;
  titleLeftButton?: string;
  onPressLeftButton?: () => void;
  colorLeftButton?: string;
  bgColorLeftButton?: string;
  titleRightButton: string;
  onPressRightButton: () => void;
  colorRightButton?: string;
  bgColorRightButton?: string;
}

export const MyConfirmationDialog = memo(
  ({
    isVisible,
    onCloseRequest,
    content,
    titleRightButton,
    titleLeftButton,
    onPressRightButton,
    onPressLeftButton,
    subContent,
    title,
    disableSwipe,
    children,
    bgColorRightButton,
    colorRightButton,
    bgColorLeftButton,
    colorLeftButton,
  }: PropsWithChildren<Props>) => {
    const theme = useTheme();

    return (
      <Modal
        isVisible={isVisible}
        onDismiss={onCloseRequest}
        onBackdropPress={onCloseRequest}
        onBackButtonPress={onCloseRequest}
        avoidKeyboard={true}
        presentationStyle={'overFullScreen'}
        swipeDirection={disableSwipe ? undefined : 'down'}
        onSwipeComplete={onCloseRequest}
        propagateSwipe={!disableSwipe}
        backdropTransitionOutTiming={0}
        hideModalContentWhileAnimating={true}
        useNativeDriver={true}
        statusBarTranslucent={true}
        animationIn={'zoomIn'}
        animationOut={'zoomOut'}>
        <SWrapper>
          <HeaderWrapper>
            <SCloseButton disabled={true} />
            <STitleHeader>{title}</STitleHeader>
            <SCloseButton source={IC_OUTLINED_CLOSE} onPress={onCloseRequest} />
          </HeaderWrapper>
          {content ? <SContent>{content}</SContent> : null}
          {subContent ? <SSubContent>{subContent}</SSubContent> : null}
          {children ? <SContainer>{children}</SContainer> : null}
          <SBottomButton
            buttons={[
              {
                title: titleLeftButton || translate('policy.cancel'),
                onPress: () => {
                  onPressLeftButton && onPressLeftButton();
                  onCloseRequest();
                },
                textColor: colorLeftButton || theme.primaryColor,
                backgroundColor: bgColorLeftButton || theme.white,
                heightButton: 40,
                style: {
                  borderWidth: 1,
                  borderColor: colorLeftButton || theme.primaryColor,
                },
                textStyle: styles.textStyle,
              },
              {
                title: titleRightButton,
                onPress: onPressRightButton,
                textColor: colorRightButton || theme.white,
                backgroundColor: bgColorRightButton || theme.primaryColor,
                textStyle: styles.textStyle,
                heightButton: 40,
              },
            ]}
            hideBottomSpace
          />
        </SWrapper>
      </Modal>
    );
  },
);

const SWrapper = styled.View`
  border-radius: 8px;
  background-color: #fff;
  overflow: hidden;
  padding: 12px 16px;
  justify-content: center;
  align-items: center;
  width: 90%;
  align-self: center;
`;

const HeaderWrapper = styled.View`
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  margin-bottom: 12px;
`;

const SCloseButton = styled(UIButton).attrs(p => ({
  imageSize: 24,
  imageStyle: {
    tintColor: p.theme.neutral01,
  },
}))`
  width: 24px;
  height: 24px;
`;

const STitleHeader = styled.Text`
  font-size: 18px;
  line-height: 24px;
  font-family: ${Fonts.Medium};
  color: ${p => p.theme.neutral1};
`;

const SContent = styled.Text`
  font-size: 14px;
  line-height: 20px;
  color: ${p => p.theme.neutral1};
  font-family: ${Fonts.Regular};
  text-align: center;
`;

const SSubContent = styled(SContent)`
  color: ${p => p.theme.blue1};
`;

const SBottomButton = styled(UIBottomButton)`
  margin-top: 24px;
  width: 100%;
`;

const SContainer = styled.View`
  width: 100%;
  margin-top: 12px;
`;

const styles = StyleSheet.create({
  textStyle: {
    fontFamily: Fonts.Medium,
    fontSize: 16,
    lineHeight: 24,
  },
});
